  #header {
      width:100%;
      height:80vh;
      margin-top: 70px;
  }

  #header h1{
      margin: 0 0 10px 0;
      font-size: 48px;
      font-weight: 700;
      color:black;
  }

  #header .brand-name{
      color:#3498db;
  }
  #header h2 {
      color:#484848;
      font-size: 24x;
  }
  #header .btn-get-started{
      font-weight: 500px;
      font-size: 16px;
      letter-spacing:  1px;
      display: inline-block;
      padding: 10px 30px;
      border-radius: 50px;
      transition:0.5s;
      color: #3498db;
      border:2px solid #3498db;
      text-decoration:none; 

  }

  #header .btn-get-started:hover{
      background: #3498db;
      color:#fff;

  }

  .header-img {
      text-align: right;
  }

  #header .animated{
      animation: up-down 2s ease-in-out infinite alternate-reverse both;

  }

  @-webkit-keyframes up-down {
      0%{
          -webkit-transform: translateY(10px);
                  transform: translateY(10px);
      }
      100% {
          -webkit-transform: translateY(-10px);
                  transform: translateY(-10px);
      }

  }
  @keyframes up-down{
      0% {
          -webkit-transform: translateY(10px);
                  transform: translateY(10px);
      }
      100%{
          -webkit-transform: translateY(-10px);
                  transform: translateY(-10px);
      }
  }


  .contact_div{
      width: 100%;
      height: 75vh;
  }

  footer p {
      padding:0;
      margin: 0;
  }

  @media (max-width:991px){
      #header{
          height:80vh;
          text-align: center;
      }

      #header .header-img{
          text-align:center;

      }
      #header .header-img img {
          width:60%;
      }
  }

  @media(max-width:768px){
      #header{
          margin-top: 20px;
      }
      #header h1{
       font-size:18px;
       margin-bottom :30px;
       
      }
      #header .header-img img {
          width:70%;
      }
  }


*{
margin: 0;
padding: 0;
box-sizing: border-box;
font-family: "Muli",sans-serif;
}

:root{
  --primary: rgb(0, 195, 255);
}

.navbar{
  background-color: rgb(7, 7, 7);
}

.navbar-brand{
  font-size:1.9rem;
  color: #eb1923;
}

.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.nav-item{
  align-items: center;
  text-align: center;
}
.menu_active{
  font-weight: bold !important;
  border-bottom: 3px solid whitesmoke;
  border-radius: 1px;
  text-align: center;
  width: auto;
  }

  .nav-itemdropdown{
    background-color: aqua;
  
  
  }

.navbar a:hover{
    color:rgb(18, 236, 164)
  }

@media(max-width:991px){
  .navbar-brand{
    font-size: 1.5rem;
  }
  .menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}
.nav-link {
    color: #fff;
    display: flex;
    align-items:center;
    text-decoration: none;
    text-align: center;
    padding: 0.5rem 1rem;
    height: 100%;
  }

  @media (min-width: 768px) {
    .navbar-nav.navbar-center {
      position: absolute;
      left: 50%;
      grid-gap: 35px;
      -webkit-transform: translatex(-50%);
              transform: translatex(-50%);
    }
  }
  @media (min-width: 768px) {
    .navbar-nav.navbar-right {
      position: absolute;
      right: 10%;
      -webkit-transform: translatex(-50%);
              transform: translatex(-50%);
    }
  }
  

